"use client";

import { useState } from "react";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Users, Copy, Check, ArrowRight } from "lucide-react";

interface PrivateRoomManagerProps {
    createdRoomCode: string;
    onCreateRoom: () => void;
    onJoinRoom: (roomId: string) => void;
}

export function PrivateRoomManager({
    createdRoomCode,
    onCreateRoom,
    onJoinRoom,
}: PrivateRoomManagerProps) {
    const [joinRoomId, setJoinRoomId] = useState("");
    const [copied, setCopied] = useState(false);

    const copyRoomCode = () => {
        navigator.clipboard.writeText(createdRoomCode);
        setCopied(true);
        setTimeout(() => setCopied(false), 2000);
    };

    return (
        <div className="flex-1 space-y-6">
            {/* Create Room */}
            <Card className="p-6">
                <h4 className="font-semibold text-white mb-4">CREATE ROOM</h4>
                {createdRoomCode ? (
                    <div className="space-y-4">
                        <div className="flex items-center gap-3">
                            <div className="flex-1 bg-background-elevated px-4 py-3 rounded font-mono text-xl text-accent">
                                {createdRoomCode}
                            </div>
                            <Button onClick={copyRoomCode} variant="secondary">
                                {copied ? <Check className="w-5 h-5" /> : <Copy className="w-5 h-5" />}
                            </Button>
                        </div>
                        <p className="text-sm text-foreground-muted">
                            Share this code with your friend. Waiting for them to join...
                        </p>
                    </div>
                ) : (
                    <Button onClick={onCreateRoom} size="lg">
                        <Users className="w-5 h-5" />
                        CREATE PRIVATE ROOM
                    </Button>
                )}
            </Card>

            {/* Join Room */}
            <Card className="p-6">
                <h4 className="font-semibold text-white mb-4">JOIN ROOM</h4>
                <div className="flex gap-3">
                    <Input
                        placeholder="Enter room code (e.g., ABCD-1234)"
                        value={joinRoomId}
                        onChange={(e) => setJoinRoomId(e.target.value.toUpperCase())}
                        className="flex-1 font-mono"
                    />
                    <Button
                        onClick={() => onJoinRoom(joinRoomId.trim())}
                        disabled={!joinRoomId.trim()}
                    >
                        JOIN
                        <ArrowRight className="w-4 h-4" />
                    </Button>
                </div>
            </Card>
        </div>
    );
}
