"use client";

import { Crown } from "lucide-react";

type ColorChoice = "white" | "black" | "random";

interface ColorSelectorProps {
    selectedColor: ColorChoice;
    onSelect: (color: ColorChoice) => void;
}

export function ColorSelector({ selectedColor, onSelect }: ColorSelectorProps) {
    return (
        <div className="mb-8">
            <h3 className="font-display text-xl mb-4 flex items-center gap-2">
                <Crown className="w-5 h-5 text-accent" />
                YOUR COLOR
            </h3>
            <div className="flex gap-3">
                <button
                    onClick={() => onSelect("white")}
                    className={`flex-1 px-6 py-4 border rounded transition-all ${selectedColor === "white"
                            ? "border-accent bg-accent/10 text-white"
                            : "border-border text-foreground-muted hover:border-accent/50"
                        }`}
                >
                    <div className="text-4xl mb-2">♔</div>
                    <div className="font-semibold">WHITE</div>
                    <div className="text-xs opacity-70">Move first</div>
                </button>

                <button
                    onClick={() => onSelect("black")}
                    className={`flex-1 px-6 py-4 border rounded transition-all ${selectedColor === "black"
                            ? "border-accent bg-accent/10 text-white"
                            : "border-border text-foreground-muted hover:border-accent/50"
                        }`}
                >
                    <div className="text-4xl mb-2">♚</div>
                    <div className="font-semibold">BLACK</div>
                    <div className="text-xs opacity-70">Move second</div>
                </button>

                <button
                    onClick={() => onSelect("random")}
                    className={`flex-1 px-6 py-4 border rounded transition-all ${selectedColor === "random"
                            ? "border-accent bg-accent/10 text-white"
                            : "border-border text-foreground-muted hover:border-accent/50"
                        }`}
                >
                    <div className="text-4xl mb-2">⚡</div>
                    <div className="font-semibold">RANDOM</div>
                    <div className="text-xs opacity-70">Surprise me</div>
                </button>
            </div>
        </div>
    );
}
